body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.noScroll::-webkit-scrollbar {
	display: none;
}

.noScroll {
	-ms-overflow-style: none;
	scrollbar-width: none;
}

/* custom scrollbar */
::-webkit-scrollbar {
	width: 5px;
}

::-webkit-scrollbar-track {
	background: var(--primary-900);
}

::-webkit-scrollbar-thumb {
	background: var(--primary-700);
}

.react-flow__attribution {
	display: none;
}

input[type="range"] {
	-webkit-appearance: none;
	width: 100%;
	height: 10px;
	border-radius: 5px;
	background-color: white;
	outline: 0.5px solid var(--primary-200);
	overflow: hidden;
}

input[type="range"]:disabled::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: var(--primary-900);
	cursor: pointer;
	box-shadow: -25.3rem 0 0 25rem var(--primary-900);
}

input[type="range"]::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: var(--primary-700);
	cursor: pointer;
	box-shadow: -25.3rem 0 0 25rem var(--primary-500);
}

.magnifier-image {
	opacity: 0;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	:root {
		--primary-50: #f2f7fd;
		--primary-100: #e5eef9;
		--primary-200: #c4dbf3;
		--primary-300: #90bde9;
		--primary-400: #559bdb;
		--primary-500: #3182ce;
		--primary-600: #2062a9;
		--primary-700: #1a4472;
		--primary-800: #2d3748;
		--primary-900: #1a202c;
		--primary-950: #171923;
	}
}
